'use client';

import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { Plus, Search } from 'lucide-react';

export default function ProductosPage() {
  const [search, setSearch] = useState('');
  const [categoria, setCategoria] = useState('');
  const [estado, setEstado] = useState('');

  return (
    <div className="space-y-8 animate-fadeIn">
      {/* ----------------------- ENCABEZADO ----------------------- */}
      <header className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <div>
          <h1 className="text-3xl font-bold tracking-tight text-white">
            Productos
          </h1>
          <p className="text-white/80">
            Gestión completa de productos, inventario y precios del sistema.
          </p>
        </div>

        <Button className="gap-2 shadow-md shadow-white/20 hover:shadow-lg hover:shadow-white/30 transition-all">
          <Plus className="w-4 h-4" />
          Nuevo producto
        </Button>
      </header>

      {/* ----------------------- FILTROS ----------------------- */}
      <Card className="border-white/20 shadow-sm bg-white/5">
        <CardHeader>
          <CardTitle className="text-white">Filtros</CardTitle>
        </CardHeader>

        <CardContent className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {/* BUSCADOR */}
          <div className="relative">
            <Search className="w-4 h-4 absolute left-3 top-1/2 -translate-y-1/2 text-white/40" />
            <Input
              placeholder="Buscar producto..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="pl-10 focus:ring-white/60"
            />
          </div>

          {/* CATEGORÍA */}
          <Select value={categoria} onValueChange={setCategoria}>
            <SelectTrigger className="focus:ring-white/60">
              <SelectValue placeholder="Categoría" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="bebidas">Bebidas</SelectItem>
              <SelectItem value="snacks">Snacks</SelectItem>
              <SelectItem value="postres">Postres</SelectItem>
              <SelectItem value="menu">Menú</SelectItem>
            </SelectContent>
          </Select>

          {/* ESTADO */}
          <Select value={estado} onValueChange={setEstado}>
            <SelectTrigger className="focus:ring-white/60">
              <SelectValue placeholder="Estado" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="activo">Activo</SelectItem>
              <SelectItem value="inactivo">Inactivo</SelectItem>
              <SelectItem value="agotado">Agotado</SelectItem>
            </SelectContent>
          </Select>
        </CardContent>
      </Card>

      {/* ----------------------- TABLA DE PRODUCTOS ----------------------- */}
      <Card className="border-white/20 shadow-sm bg-white/5">
        <CardHeader>
          <CardTitle className="text-white">Listado de productos</CardTitle>
        </CardHeader>

        <CardContent>
          <div className="border-t border-white/20 pt-4">
            <p className="text-white/70 text-sm">
              Aquí irá la tabla de productos.  
              Esta tarjeta ya está preparada para integrar una tabla dinámica (DataTable) con acciones como editar, eliminar, stock y precios.
            </p>

            <div className="mt-4 p-4 bg-white/10 rounded-xl text-white/70">
              <span className="font-semibold">Sugerencia:</span> puedes integrar aquí tu componente de tabla o un DataTable de Shadcn para una experiencia más profesional.
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
